import React, { Component } from 'react';
// import axios from 'axios';

export default class Conversor_moeda extends Component {
    
    constructor(props){
        super(props)
        this.state ={
            //valor_moeda:"",
            //moeda: "",
            
        }
        this.cotacao = this.cotacao.bind(this)

    }
    

    
    cotacao(){
        
        // axios.get(`https://economia.awesomeapi.com.br/last/USD-BRL`).then(response =>
        // console.log(response.data.USDBRL.ask));
        // O axios acima é apenas para usar o console.log para ver se esta funcionando no console
        let url = 'https://economia.awesomeapi.com.br/last/BRL-USD'
        fetch(url)
        .then(res=>{
            return res.json()
        })
        .then(json=>{
            var valor_moeda = json['BRLUSD'].ask
            this.setState({valor_moeda})
            let moeda = parseFloat(this.state.moeda).toFixed(2)
            this.setState({moeda}) 
            let resultado = parseFloat(valor_moeda * moeda).toFixed(2) 
            this.setState({resultado}) 
            //TEXTO NO JSX, HTML
            let agora = `REAL AGORA - U$1.00 = R$${parseFloat(this.state.valor_moeda).toFixed(2)}`
            this.setState({agora})
            let valor_dig = `R$${this.state.moeda} hoje é igual:`
            this.setState({valor_dig})
            let valor_final = `U$${this.state.resultado} Dolares`
            this.setState({valor_final})
        })
        
        console.log(this.state)
        this.setState({
            // valor_moeda:"",
            //moeda:"",
            
        })

        
    }


    render(){
        return (
            <div>
            <h2>BRL para USD</h2>
            <h2>{this.state.agora}</h2>
            <nobr>R$</nobr>
            <input type="number" id="valor_input" step=".01" title="Digite apenas números e ponto para centavos" onChange={(event)=>{this.setState({moeda:event.target.value})}}></input>
            <button type="button" onClick={this.cotacao}>Converter</button>
            <h2>{this.state.valor_dig} {this.state.valor_final}</h2>
            
        </div>
        )
        
    }
}

